import React, {
  FC,
  memo,
  useReducer,
  useRef,
  useEffect,
} from "react";
import {
  createSmartappDebugger,
  createAssistant,
  AssistantAppState,
} from "@sberdevices/assistant-client";
import "./App.css";
import { Button, Image } from '@salutejs/plasma-ui';
import { headline2} from '@salutejs/plasma-tokens';
import task from './assets/task.png'
import ListItem from "./components/list-item/ListItem";
import { reducer } from "./store";

const initializeAssistant = (getState: any) => {
  if (process.env.NODE_ENV === "development") {
    console.log(getState)
    return createSmartappDebugger({
      token: process.env.REACT_APP_TOKEN ?? "",
      initPhrase: ` ${process.env.REACT_APP_SMARTAPP}`,
      getState,
    });
  }

  return createAssistant({ getState });
};

export const App: FC = memo(() => {
  //const [appState, dispatch] = useReducer(reducer, { notes: [] });
  const list = [
    {
      title: "    ",
      subtitle: "  ,   "
    },
    {
      title: "       ID",
      subtitle: "       "
    },
    {
      title: "   ",
      subtitle: "         "
    },
  ]

  const assistantStateRef = useRef<AssistantAppState>();
  const assistantRef = useRef<ReturnType<typeof createAssistant>>();

  useEffect(() => {
    assistantRef.current = initializeAssistant(() => assistantStateRef.current);

    // assistantRef.current.on("data", ({ action }: any) => {
    //   if (action) {
    //     dispatch(action);
    //   }
    // });
  }, []);

  return (
    <main className="container">
      <Image
        src={task}
        width="160px"
        height="160px"
        alt="  "
      />
      <h2 style={headline2}>
        
        <br />
        " "
      </h2>
      <div style={{ display: "flex", flexDirection: "column", }}>
        {
          list.map((item, index) =>
            <ListItem key={index} index={index + 1} title={item.title} subtitle={item.subtitle} />
          )
        }
        <Button text="   " size="m" view="primary" />
        {/* <p >
                    
        </p> */}
      </div>
    </main>
  );
});
